<template>
  <div id="app">
    <div class="weui-tab__panel">
      <circle-menu :animate='AnimateType' :type="TypeValue" :mask='MaskType' id="menu" :circle='isCircle' :btn='isBtn' class="ab" v-show="MenuBottom" :number='NumberType'>
        <a slot="item_1" class="fa fa-twitter fa-lg" @click='item1'></a>
        <a slot="item_2" class="fa fa-weixin fa-lg" @click='item2'></a>
        <a slot="item_3" class="fa fa-weibo fa-lg" @click='item3'></a>
        <a slot="item_4" class="fa fa-github fa-lg" @click='item4'></a>
      </circle-menu>  
      <div class="weui-msg">
        <div class="weui-msg__icon-area">
          <img src="https://avatars0.githubusercontent.com/u/21058462?v=3&s=460" width="35%" style="border-radius: 50%;" >
        </div>
        <div class="weui-msg__text-area">
          <p class="weui-msg__desc">演示需要,引入了animate.css和font-awsome</p>
          <p class="weui-msg__desc">colors属性不好演示，具体用法参考文档</p>
          <p class="weui-msg__desc">版本 V1.1.1</p>
        </div>
        <div class="weui-msg__extra-area">
          <div class="weui-footer">
            <p class="weui-footer__text">Copyright &copy; 2008-2016 weui.io</p>
          </div>
        </div>
      </div>
      <div id="box">
        <div class="weui-cells" >
          <selector title="type" :options="type" v-model="TypeValue"></selector>
          <selector title="number" :options="number" v-model="NumberType"></selector> 
          <selector title="mask" :options="mask" v-model="MaskType"></selector>
          <selector title="animate" :options="animate" v-model="AnimateType"></selector>
          <!--<selector title="colors" :options="colors" v-model="ColorType" @click='changeType'></selector>-->
          <switcher title="circle" v-model="isCircle"></switcher>          
          <switcher title="btn" v-model="isBtn"></switcher>
        </div>
      </div> 
      <br>
      <div class="weui-footer">
        <p class="weui-footer__links">
            <a @click='' class="weui-footer__link">VueCircleMenu</a>
        </p>
        <p class="weui-footer__text">Copyright &copy; by web-oysun@qq.com</p>
      </div>   
    </div>
    <div class="weui-tabbar nav">
        <div class="weui-tabbar__item weui-flex" v-show="MenuLeft">
          <div class="weui-flex__item"><div class="placeholder">weui</div></div>
          <circle-menu :animate='AnimateType' :type="TypeValue" :mask='MaskType' id="menu" :circle='isCircle' :btn='isBtn' :number='NumberType'>
            <a slot="item_1" class="fa fa-twitter fa-lg" @click='item1'></a>
            <a slot="item_2" class="fa fa-weixin fa-lg" @click='item2'></a>
            <a slot="item_3" class="fa fa-weibo fa-lg" @click='item3'></a>
            <a slot="item_4" class="fa fa-github fa-lg" @click='item4'></a>
          </circle-menu>
          <div class="weui-flex__item"><div class="placeholder">weui</div></div>
        </div>
        <a href="javascript:;" class="weui-tabbar__item weui-bar__item_on">
            <img src="http://node-os.com/images/nodejs.png" alt="" class="weui-tabbar__icon">
            <p class="weui-tabbar__label">设计</p>
        </a>
        <div class="weui-tabbar__item weui-flex" v-show="MenuMiddle">
          <div class="weui-flex__item"><div class="placeholder">weui</div></div>
          <circle-menu :animate='AnimateType' :type="TypeValue" :mask='MaskType' id="menu" :circle='isCircle' :btn='isBtn' :number='NumberType'>
            <a slot="item_1" class="fa fa-twitter fa-lg" @click='item1'></a>
            <a slot="item_2" class="fa fa-weixin fa-lg" @click='item2'></a>
            <a slot="item_3" class="fa fa-weibo fa-lg" @click='item3'></a>
            <a slot="item_4" class="fa fa-github fa-lg" @click='item4'></a>
          </circle-menu>
          <div class="weui-flex__item"><div class="placeholder">weui</div></div>
        </div>
        <a href="javascript:;" class="weui-tabbar__item weui-bar__item_on">
            <img src="http://cn.vuejs.org/images/logo.png" alt="" class="weui-tabbar__icon">
            <p class="weui-tabbar__label">编程</p>
        </a>
        <div class="weui-tabbar__item weui-flex" v-show="MenuRight">
          <div class="weui-flex__item"><div class="placeholder">weui</div></div>
          <circle-menu :animate='AnimateType' :type="TypeValue" :mask='MaskType' id="menu" :circle='isCircle' :btn='isBtn' :number='NumberType'>
            <a slot="item_1" class="fa fa-twitter fa-lg" ></a>
            <a slot="item_2" class="fa fa-weixin fa-lg" ></a>
            <a slot="item_3" class="fa fa-weibo fa-lg" ></a>
            <a slot="item_4" class="fa fa-github fa-lg" ></a>
          </circle-menu>
          <div class="weui-flex__item"><div class="placeholder">weui</div></div>
        </div>
    </div>
  </div>
</template>

<script>
import selector from './components/form/selector'
import switcher from './components/form/switch'
import CircleMenu from './components/CircleMenu.js'
export default {
  name: 'app',
  components: {
    CircleMenu,
    selector,
    switcher
  },
  data () {
    return {
      TypeValue: 'top',
      MaskType: '',
      AnimateType: '',
      // ColorType: [],
      isCircle: false,
      isBtn: false,
      NumberType: 4,
      type: [ 'top', 'bottom', 'left', 'right', 'middle', 'middle-around' ],
      mask: [ '无', 'white', 'black' ],
      animate: [ '默认', 'animated rubberBand', 'animated jello', 'animated swing', 'animated flash', 'animated bounce', 'animated flip' ],
      // colors: [ '默认', '自定义' ]
      number: [2, 3, 4]
    }
  },
  computed: {
    MenuLeft () {
      return this.TypeValue === 'left'
    },
    MenuRight () {
      return this.TypeValue === 'right'
    },
    MenuBottom () {
      return this.TypeValue === 'bottom'
    },
    MenuMiddle () {
      return this.TypeValue !== 'left' && this.TypeValue !== 'right' && this.TypeValue !== 'bottom'
    }
  },
  methods: {
    item1 () {
      alert('你点击了第1个按钮')
    },
    item2 () {
      alert('你点击了第2个按钮')
    },
    item3 () {
      alert('你点击了第3个按钮')
    },
    item4 () {
      alert('你点击了第4个按钮')
    }
  }
}
</script>

<style lang="less">
@import './less/weui';
body {
    -webkit-overflow-scrolling: touch;
}
.ab {
  position: absolute;
  top: 30%;
  left: 3%;
}
body {
  background-color: #eee;
  font-family: "Microsoft YaHei";
  
}
#menu{

  & a{
    color: white;
    font-size: 14px;
    text-align: center;
  }
}
#menu-left{
  
  & a{
    color: white;
  }
}
.nav{
  position: fixed;
}
#box{
  display: flex;
  justify-content: space-around;
}
</style>



