<template>
  <div class="weui-cell weui-cell_switch">
    <div class="weui-cell__bd">{{ title }}</div>
    <div class="weui-cell__ft">
      <input class="weui-switch" type="checkbox" v-model="currentValue" />
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentValue: this.value
    }
  },
  props: {
    title: String
  },
  watch: {
    currentValue (val) {
      this.$emit('input', val)
    }
  }

}
</script>

<style scoped>

</style>


