<template>
  <div class="weui-cell weui-cell_select weui-cell_select-after">
    <div class="weui-cell__hd">
      <label for="" class="weui-label">{{ title }}</label>
    </div>
    <div class="weui-cell__bd">
      <select class="weui-select" v-model="currentValue">
        <option :value="one" v-for="one in options">{{one}}</option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  name: 'selector',
  props: {
    title: String,
    options: Array
  },
  data () {
    return {
      currentValue: ''
    }
  },
  watch: {
    currentValue (val) {
      this.$emit('input', val)
    }
  }

}
</script>



